.article__item {
  display: flex;
  justify-content: center;
  margin: 0 auto 4rem auto;
}
.article__item .img__wrapper {
  width: 505px;
  height: 330px;
  cursor: pointer;
}
.article__item .img__wrapper:hover {
  filter: contrast(125%);
}
.article__item .divider {
  width: 95px;
}
.article__item .article__content {
  width: 380px;
  align-self: center;
}
.article__item .article__content .article__title {
  font-size: 2.625rem;
  margin-bottom: 1.5rem;
}
.article__item .article__content .article__text {
  margin-bottom: 2rem;
  line-height: 150%;
  font-weight: 300;
}
.article__item .article__content .btn__counter {
  background: #E4C1AE;
  border: none;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  color: white;
  font-weight: 700;
  transition: 0.25s ease-in-out;
  cursor: pointer;
}
.article__item .article__content .btn__counter:hover {
  color: #121212;
  background: wheat;
}
.article__item:nth-child(even) {
  flex-direction: row-reverse;
  text-align: right;
  background: rgb(240, 238, 234);
}
.article__item:nth-child(even) .img__wrapper {
  width: 505px;
  height: 330px;
  pointer-events: none;
}
.article__item:nth-child(even) .img__wrapper:hover {
  filter: none;
}
.article__item:nth-child(even) .btn__counter {
  background: grey;
  cursor: not-allowed;
  pointer-events: none;
}

@media (max-width: 992px) {
  .article__item {
    margin: 0 auto 4rem auto;
    padding: 0 0.5rem;
  }
  .article__item .img__wrapper {
    width: 100%;
    height: auto;
  }
  .article__item .divider {
    width: 50px;
  }
  .article__item .article__content {
    width: 100%;
    align-self: flex-start;
  }
  .article__item .article__content .article__title {
    font-size: 2.25rem;
  }
  .article__item:nth-child(even) {
    text-align: right;
    background: rgb(240, 238, 234);
    padding: 1rem 0.5rem;
  }
  .article__item:nth-child(even) .img__wrapper {
    width: 100%;
    height: auto;
  }
  .article__item:nth-child(even) .img__wrapper:hover {
    filter: none;
  }
  .article__item:nth-child(even) .btn__counter {
    background: grey;
    cursor: not-allowed;
    pointer-events: none;
  }
}
@media (max-width: 768px) {
  .article__item {
    margin: 0 auto 2rem auto;
    flex-direction: column;
  }
  .article__item .img__wrapper {
    width: 100%;
    height: auto;
  }
  .article__item .divider {
    width: 0px;
    height: 1rem;
  }
  .article__item .article__content {
    width: 95%;
    align-self: center;
  }
  .article__item .article__content .article__title {
    font-size: 2rem;
  }
  .article__item:nth-child(even) {
    flex-direction: column;
    text-align: right;
    background: rgb(240, 238, 234);
  }
  .article__item:nth-child(even) .img__wrapper {
    width: 100%;
    height: auto;
  }
  .article__item:nth-child(even) .img__wrapper:hover {
    filter: none;
  }
  .article__item:nth-child(even) .btn__counter {
    background: grey;
    cursor: not-allowed;
    pointer-events: none;
  }
}